.footer {
  max-width: 100%;
  min-height: 215px;
  padding: 58px 0 80px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}

.footer__logo {
  max-width: 140px;
}

.menu__list {
  display: flex;
  flex-direction: column;
  list-style: none;
}

.menu__list li:not(:last-child) {
  margin-bottom: 10px;
}

.menu__list__item,
.menu__list__social__item {
  display: inline-block;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.74);
}

.menu__list__item:hover {
  transition: 1s;
  opacity: 0.5;
}

.menu__list__title {
  text-align: center;
  margin-bottom: 5px;
}

.menu__list__social {
  display: flex;
  list-style: none;
}

.menu__list__social li:not(:last-child) {
  margin-right: 9px;
}

.menu__list__social__item {
  width: 20px;
  height: 20px;
}

.instagram {
  background: url(../../assets/svg/instagram.svg) no-repeat center/100%;
}

.vk {
  background: url(../../assets/svg/vk2.svg) no-repeat center/100%;
}

.telegram {
  background: url(../../assets/svg/telegram.svg) no-repeat center/100%;
}

.youtube {
  background: url(../../assets/svg/youtube.svg) no-repeat center/100%;
}


/*для скринридеров*/
.footer__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}